.page-content a {
  text-decoration: underline;
}

h1,
.main-title,
.portal-title,
.h1,
.title-1 {
  color: #1351b4;
  font-size: 2.25rem;
  font-weight: 500;
  line-height: 1.5;
  margin-bottom: 40px;
}

@media (max-width: 575.98px) {
  h1,
  .main-title,
  .portal-title,
  .h1,
  .title-1 {
    font-size: 1.625rem;
  }
}

h2,
.portal-big-news-title,
.h2,
.title-2 {
  color: #1351b4;
  font-size: 1.5rem;
  font-weight: 500;
  line-height: 1.5;
  margin-bottom: 40px;
}

h3,
.h3,
.portal-small-news-title,
.subtitle-1,
.portal-big-news,
.portal-feature-news {
  color: #1351b4;
  font-size: 1.25rem;
  font-weight: 500;
  margin-bottom: 0;
}

h4,
.h4 .subtitle-2 {
  color: #1351b4;
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 0;
}

.label {
  color: #333;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 16px;
}

fieldset legend,
.fieldset {
  color: #1351b4;
  font-size: 1.375rem;
  font-weight: 500;
  margin-bottom: 24px;
}

.help {
  color: #333;
  font-size: 0.8125rem;
}

.feedback {
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 500;
  padding: 4px;
}

.feedback.is-valid {
  color: #168821;
}

.feedback.is-invalid {
  color: #e60000;
}

.divider {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
}

.divider::after, .divider::before {
  background: #ccc;
  content: "";
  height: 1px;
  width: 100%;
}

